import { useState } from 'react';
import { columns, TableData} from '../../state/makeData';
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';

export interface ITableProps {
    data: TableData[]; // статус фильтров пока не описан полностью 
}

export const MobileTable = ({data}: ITableProps) => {
    const [showChildren, getShowChildren]: [number[], any] = useState([]);

    const toogleChildrenVisible = (key: number) => {
        const k: number | -1 = showChildren.indexOf(key);
        console.log(showChildren.splice(k, 1));
        getShowChildren( k ===  -1 ? showChildren.concat([key]) : showChildren.splice(k, 1));
    }
    return (
        <Table>
            <Thead>
                <Tr>
                    {columns.map(el => (
                        <Th>{el.title}</Th>
                    ))}
                </Tr>
            </Thead>
            <Tbody>
                {data.map( i => {
                    return ([
                        <Tr className={i.children ? 'table-mobile-main' : ''}>
                            {columns.map( c => {
                                const element = Object.entries(i).find(l  =>  l[0].includes(c.dataIndex));
                                if (!element) return null;
                                return (
                                    <Td className={c.className}>{element[1]}</Td>
                                )
                            })}
                        </Tr>,
                        i.children  && showChildren.includes(i.key) && <MobileTable data={i.children}/>,
                        i.children &&  <div className="table-show-more" onClick={() => toogleChildrenVisible(i.key)}>{!showChildren.includes(i.key) ? <><DownOutlined />Show more</> : <><UpOutlined />Hide </>}</div>,
                    ])
                })}
            </Tbody>
        </Table>
    )
}


