import {useState, useEffect} from 'react';
import { Spin } from 'antd';
import { tableData, TableData} from '../../state/makeData';
import { DesktopTable } from './desktopTable';
import { MobileTable } from './mobileTable';

import './styles.css';

export interface ITableProps {
  loadStatus: boolean;
  filters: any; // статус фильтров пока не описан полностью 
}

export const Tables = ({loadStatus, filters}: ITableProps) => {
  const [mode, setMode] = useState<string>('desktop');

  useEffect(() => {
    handlerResize();
    window.addEventListener('resize', handlerResize, false);
    return () => {
      window.removeEventListener('resize', handlerResize);
    }
  }, [])

  const handlerResize = () => {
     if (window.innerWidth < 700) {
        setMode('mobile');
      } else {
        setMode('desktop');
      }
  }

  const data: TableData[]= tableData.filter( (el: TableData) => {
    if (filters?.geo?.length) {
        return filters?.geo.includes(el.date);
    } else {
        return true;
    }
  });

  return (
    <Spin size="large" spinning={loadStatus}>
      {mode === 'mobile' ? <MobileTable data={data}/> : <DesktopTable data={data}/> }
    </Spin>
  )
}
